import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

const getUrl = (name, id, sub) => `http://invest-crot-invest-crot.b9ad.pro-us-east-1.openshiftapps.com/api/${name}/${id ? id+'/' : ''}${sub ? sub+'/' : ''}`;

export default new Vuex.Store({
  state: {
    auth: {
      token: localStorage.getItem('user-token') || '',
      userId: localStorage.getItem('user-id') || '',
    },
    user: {},
    userProjects: [],
    params: {
      city: [],
      industry: [],
      stage: []
    },
    projects: {
      message: [],
      paginate: {}
    },
    project: {},
    load: false
  },
  getters: {
    isAuthenticated: state => !!state.auth.token
  },
  mutations: {
    saveParams(store, newParams) {
      store.params = newParams;
    },
    saveProjects(store, newProjects) {
      store.projects = newProjects;
    },
    saveProject(store, newProject) {
      store.project = newProject;
    },
    saveUserProjects(store, newUserProjects) {
      store.userProjects = newUserProjects;
    },
    toggleLoad(store, newLoad) {
      store.load = newLoad;
    },
    authUser(state, data) {
      if (data) {
        state.auth.token = data.token;
        state.auth.userId = data.userId;
      } else {
        state.auth.token = false;
        state.auth.userId = false;
      }
    },
    saveUser(state, data) {
      state.user = data;
    }
  },
  actions: {
    loadParams({ commit }) {
      return axios.get(getUrl('params')).then(({ data }) => {
        commit('saveParams', data.message);
      });
    },
    loadProjects({ commit }, data) {
      return axios.get(getUrl('projects'), data).then(({ data }) => {
        commit('saveProjects', data);
      });
    },
    loadProject({ commit }, id) {
      return axios.get(getUrl('projects', id)).then(({ data }) => {
        commit('saveProject', data);
      });
    },
    addUser({}, data) {
        return axios.post(getUrl('registration'), data);
    },
    toggleLoad({ commit }, data) {
      commit('toggleLoad', data);
    },
    authUser({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post(getUrl('login'), data).then(({ data }) => {
          const token = 'Bearer ' + data.token;
          const userId = data.userId;

          commit('authUser', { token, userId });

          localStorage.setItem('user-token', token);
          localStorage.setItem('user-id', userId);

          axios.defaults.headers.common['Authorization'] = token;

          resolve();
        }).catch((error) => {
          commit('authUser', false);

          localStorage.removeItem('user-token');
          localStorage.removeItem('user-id');

          reject(error);
        });
      })
    },
    logoutUser({ commit }) {
      return new Promise((resolve, reject) => {
        commit('authUser', false);

        localStorage.removeItem('user-token');
        localStorage.removeItem('user-id');

        commit('saveUser', {});

        resolve();
      });
    },
    getUser({ commit }) {
      return axios.get(getUrl('user'), { params: { id: this.state.auth.userId } }).then(({ data: { message } }) => {
        commit('saveUser', message);
      });
    },
    getUserProjects({ commit }) {
      return axios.get(getUrl('user', this.state.auth.userId, 'projects')).then(({ data: { message } }) => {
        commit('saveUserProjects', message);
      })
    },
    deleteProject({ commit }, id) {
      return axios.delete(getUrl('projects', id));
    },
    addProject({}, data) {
      return axios.post(getUrl('add-project'), data);
    }
  }
});
