<template>
  <div id="app">
    <div v-show="getLoad" class="preloader">
      <div class="preloader__load"></div>
    </div>
    <Header></Header>
    <router-view/>
  </div>
</template>

<script>
  import Header from './components/Header.vue';
  import { mapState, mapActions, mapGetters } from 'vuex';

  export default {
    name: "App",
    components: {
      Header
    },
    computed: {
      ...mapState({
        'getLoad': state => state.load,
      }),
      ...mapGetters(['isAuthenticated'])
    },
    methods: {
      ...mapActions(['getUser'])
    },
    created() {
      if (this.isAuthenticated) {
        this.getUser({});
      }
    }
  }
</script>

